import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { usePapaParse } from 'react-papaparse';
import mainLogo from './img/header.png';
import GridLayout from "react-grid-layout";

function ReadRemoteFile() {
  const { readRemoteFile } = usePapaParse();
  const [inputs, setInputs] = useState({
    donorName: "",
    year: 0,
  });

  const handleReadRemoteFile = (event) => {
    event.preventDefault();
    if (validateForm()) {
      // Form has valid data, submit 
      console.log('Results:', inputs);
    } else {
      // Form is not ready Ignore
    }

    readRemoteFile('https://docs.google.com/spreadsheets/d/e/2PACX-1vQrZmlely-H39hZicEtG0SmCckhQsTdhatMReyV-MbPhBBm8LN5iNgTF1nErJqUbkAGSwWEwrCIQx4m/pub?gid=1820918316&single=true&output=csv', {
      complete: (results) => {
        let searchResults = results.data.filter((element) => element[0].includes(inputs.donorName));
        console.log('---------------------------');
        console.log('CSV Results:', results);
        console.log('Search Results', searchResults);
        let table = document.getElementById('Results');

        for (let resultsRow of searchResults) {
          //Insert a new 
          table.insertRow();
          //navigate results array
          for (let cell of resultsRow) {
            let newCell = table.rows[table.rows.length - 1].insertCell();
            newCell.textContent = cell;
          }
        }
        console.log('---------------------------');
      },
    });
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setInputs((prevState) => ({ ...prevState, [name]: value }));
  }

  const validateForm = () => {
    const errors = {};
    let isError = false;

    // Check if donorName is empty
    if (!inputs.donorName) {
      isError = true;
    }

    // Check if year is original
    if (inputs.year === 0 && isError) {
      errors.password = "Please Enter data";
    }

    setInputs((prevState) => ({ ...prevState, errors }));

    // Return true if there are no errors
    return Object.keys(errors).length === 0;
  };

  const style1 = { 'paddingTop': '12px' };
  const style2 = { 'paddingBottom': '12px' };
  const style3 = { 'textAlign': 'left' };
  const style4 = { 'backgroundColor': '#04AA6D' };
  const style5 = { 'color': 'white' };

  const layout = [
    { i: "a", x: 0, y: 0, w: 1, h: 8},
    { i: "b", x: 1, y: 0, w: 1, h: 1}  
  ];

  return (
    <div>
      <GridLayout
        layout={layout}
        cols={1}
        rowHeight={50}
        width={300}
      >
<div key="a">
                <img key="b" src={mainLogo} alt="Munesswaran Temple" />
</div>
<div key="b">
                < div>
                  <form id="requestForm" onSubmit={handleReadRemoteFile}>
                    <label>
                      Enter a Name:
                      <input
                        type="text"
                        name="donorName"
                        value={inputs.donorName || ""}
                        onChange={handleChange} />
                    </label>
                    <br />
                    <label>
                      Enter a Year:
                      <input
                        type="number"
                        name="year"
                        value={inputs.year || 0}
                        onChange={handleChange} />
                    </label>
                    <br />
                    <input
                      type="submit"
                      value="Search"
                    />
                  </form>
                </div>
                < div>
                  <table id="Results">
                    <thead>
                      <tr>
                        <th style={{ ...style1, ...style2, ...style3, ...style4, ...style5 }}> Name </th>
                        <th style={{ ...style1, ...style2, ...style3, ...style4, ...style5 }}> Year </th>
                        <th style={{ ...style1, ...style2, ...style3, ...style4, ...style5 }}> Donation </th>
                      </tr>
                    </thead>
                  </table>
                </div>
                </div>
        </GridLayout>
    </div>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <ReadRemoteFile />
  </React.StrictMode>
);


// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
